import React, { useEffect, useState } from "react";
import style from "./index.module.scss";

import clsx from "clsx";
import Lottie from "react-lottie-player/dist/LottiePlayerLight";
import { useWindowResize } from "beautiful-react-hooks";

import WithPadding from "@components/WithPadding";
import SectionHeader from "@components/SectionHeader";
import Button from "@components/Button";
import InlineLoader from "@components/InlineLoader";
import CongratsModal from "@components/CongratsModal";

import CourseModal from "./CourseModal";

import RedButterflyLottie from "@assets/courses/red_butterfly.json";
import BlueButterflyLottie from "@assets/courses/blue_butterfly.json";
import FlowerLottie from "@assets/courses/flower.json";
import TreeLottie from "@assets/courses/tree_rustling.json";
import MonkeyLottie from "@assets/courses/monkey.json";
import ShiningStarsLottie from "@assets/courses/shining_stars.json";
import DeerImg from "@assets/courses/deer.svg";
import ElephantLottie from "@assets/courses/elephant.json";
import MovingClouds from "@assets/courses/moving_cloud.json";

import StarImg from "@assets/courses/star.svg";
import BugImg from "@assets/courses/bug.svg";

import BulbLottie from "@assets/courses/bulb.json";
import InnovationLottie from "@assets/courses/innovation.json";
import PenPaperLottie from "@assets/courses/pen_paper.json";

import useCourses from "./useCourses";
import Enquiry from "./Enquiry";

const _LearnCards = [
  {
    title: "Creative Thinking",
    img: "",
    lottie: PenPaperLottie,
    size: 140,
  },
  {
    title: "Innovative Thinking",
    img: "",
    lottie: BulbLottie,
    size: 120,
  },
  {
    title: "Logical Thinking",
    img: "",
    lottie: InnovationLottie,
    size: 150,
  },
];

export default function Courses() {
  const { state, changeActiveCat, toggleModal, toggleEnquiryModal } = useCourses();
  const { catTabs, coursesByCat, activeCat, isLoading } = state;

  const courses = coursesByCat[activeCat];

  const [lottieSizes, setLottieSizes] = useState({ treeMonkeyFactor: 1 });

  function adjustTreeMonkeyFactor() {
    if (window.innerWidth < 1000 && window.innerWidth > 500) {
      setLottieSizes((prev) => ({ ...prev, treeMonkeyFactor: 0.9 }));
    }
    if (window.innerWidth < 500) {
      setLottieSizes((prev) => ({ ...prev, treeMonkeyFactor: 0.9 }));
    }
  }

  useWindowResize(adjustTreeMonkeyFactor);

  useEffect(() => {
    adjustTreeMonkeyFactor();
  }, []);

  return (
    <main className={style.wrapper}>
      {isLoading ? (
        <InlineLoader height="90vh" />
      ) : (
        <>
          <section className={style.banner}>
            <div className={clsx(style.graphic, style.star, style.star1)}>
              <img src={StarImg} alt="" />
            </div>
            <div className={clsx(style.graphic, style.star, style.star2)}>
              <img src={StarImg} alt="" />
            </div>
            <div className={clsx(style.graphic, style.star, style.star3)}>
              <img src={StarImg} alt="" />
            </div>
            <div className={clsx(style.graphic, style.star, style.star4)}>
              <img src={StarImg} alt="" />
            </div>

            <div className={clsx(style.graphic, style.bug)}>
              <img src={BugImg} alt="" />
            </div>

            <div className={clsx(style.lottie, style.movingClouds)}>
              <Lottie loop animationData={MovingClouds} play style={{ width: 400, height: 400 }} />
            </div>

            <div className={clsx(style.lottie, style.redButterfly)}>
              <Lottie loop animationData={RedButterflyLottie} play style={{ width: 60, height: 60 }} />
            </div>
            <div className={clsx(style.lottie, style.flowerWithFly)}>
              <Lottie loop animationData={FlowerLottie} play style={{ width: 60, height: 60 }} />
              <div className={style.fly}>
                <Lottie loop animationData={BlueButterflyLottie} style={{ width: 110, height: 110 }} play />
              </div>
            </div>

            <div className={clsx(style.lottie, style.elephant)}>
              <Lottie loop animationData={ElephantLottie} style={{ width: 400, height: 400 }} play />
            </div>
            <div className={clsx(style.graphic, style.deer)}>
              <img src={DeerImg} alt="" />
            </div>

            <div className={clsx(style.lottie, style.shiningStars)}>
              <Lottie loop animationData={ShiningStarsLottie} style={{ width: 200, height: 200 }} play />
            </div>

            <div className={clsx(style.monkeyTrees)}>
              <div className={clsx(style.lottie, style.flowerInTrees)}>
                <Lottie loop animationData={FlowerLottie} play style={{ width: 60, height: 80 }} />
              </div>
              <div className={clsx(style.lottie, style.treeLeft)}>
                <Lottie
                  loop
                  animationData={TreeLottie}
                  style={{ width: 450 * lottieSizes.treeMonkeyFactor, height: 450 * lottieSizes.treeMonkeyFactor }}
                  play
                />
              </div>
              <div className={clsx(style.lottie, style.treeRight)}>
                <Lottie
                  loop
                  animationData={TreeLottie}
                  style={{ width: 600 * lottieSizes.treeMonkeyFactor, height: 600 * lottieSizes.treeMonkeyFactor }}
                  play
                />
              </div>
              <div className={clsx(style.lottie, style.monkey)}>
                <Lottie
                  loop
                  animationData={MonkeyLottie}
                  style={{ width: 200 * lottieSizes.treeMonkeyFactor, height: 200 * lottieSizes.treeMonkeyFactor }}
                  play
                />
              </div>
            </div>
          </section>

          <WithPadding className={style.tabs}>
            {catTabs.map((tab) => (
              <button
                key={tab}
                className={clsx(activeCat === tab && style.active)}
                onClick={changeActiveCat.bind(this, tab)}
              >
                {tab}
              </button>
            ))}
          </WithPadding>

          <section className={style.coursesSection}>
            <WithPadding>
              <section className={style.courses}>
                {courses.map((course, index) => (
                  <article key={course.id} className={clsx(style.course, index % 2 && style.alt)}>
                    <div onClick={() => toggleModal(course)} className={style.image}>
                      <img src={course.image} alt="" />
                      <div className={style.learnMoreBtn}>
                        <button>Learn More</button>
                      </div>
                    </div>
                    <div className={style.content}>
                      <div className={style.desc}>{course.description}</div>
                      <div className={style.button}>
                        <Button onClick={() => toggleEnquiryModal(course)}>Enquire Now</Button>
                      </div>
                    </div>
                  </article>
                ))}
              </section>
            </WithPadding>
          </section>

          <WithPadding className={style.learnSection}>
            <header>
              <SectionHeader>Your Kids Will Learn</SectionHeader>
            </header>

            <section className={style.cards}>
              {_LearnCards.map((item) => (
                <article key={item.title}>
                  <div className={style.icon}>
                    <Lottie loop animationData={item.lottie} play style={{ width: item.size, height: item.size }} />
                  </div>
                  <div className={style.title}>{item.title}</div>
                </article>
              ))}
            </section>
          </WithPadding>
        </>
      )}

      <CourseModal
        activeCourse={state.activeCourse}
        isOpen={state.isOpenModal}
        onClose={toggleModal}
        courseId={state.activeModalId}
        toggleEnquiryModal={toggleEnquiryModal}
      />
      {state.isOpenEnquiryModal && (
        <Enquiry activeCourse={state.activeCourse} course={state.activeEnquiryCourse} onClose={toggleEnquiryModal} />
      )}
      {state.isOpenCongratsModal && <CongratsModal />}
    </main>
  );
}
