import { useEffect, useReducer } from "react";
import { getCourses } from "@apis/courses";

/*
   Terminology: 
   cat => category
   act => active
*/

const initialState = {
  allCourses: [],
  coursesByCat: {},
  catTabs: [],
  activeCat: "",
  isLoading: true,
  isOpenModal: false,
  isOpenCongratsModal: false,
  activeModalId: "",
  activeEnquiryCourse: {},
  activeCourse: {},
};

function reducer(state, action) {
  const { type, payload } = action;
  switch (type) {
    case "SET_ALL_COURSES":
      return { ...state, allCourses: payload.allCourses };
    case "SET_COURSES_BY_CAT":
      return { ...state, coursesByCat: payload.coursesByCat };
    case "SET_CAT_TABS":
      return { ...state, catTabs: payload.catTabs };
    case "SET_ACTIVE_CAT":
      return { ...state, activeCat: payload.activeCat };
    case "SET_IS_LOADING":
      return { ...state, isLoading: payload.isLoading };
    case "SET_ACTIVE_MODAL_ID":
      return { ...state, activeModalId: payload.activeModalId };
    case "SET_IS_OPEN_MODAL":
      return { ...state, isOpenModal: payload.isOpenModal };
    case "SET_IS_OPEN_ENQUIRY_MODAL":
      return { ...state, isOpenEnquiryModal: payload.isOpenEnquiryModal };
    case "SET_IS_OPEN_CONGRATS_MODAL":
      return { ...state, isOpenCongratsModal: payload.isOpenCongratsModal };
    case "SET_ACT_COURSE":
      return { ...state, activeCourse: payload.activeCourse };
    case "SET_ACT_ENQUIRY_COURSE":
      return { ...state, activeEnquiryCourse: payload.activeEnquiryCourse };
    default:
      return state;
  }
}
export default function useCourses() {
  const [state, dispatch] = useReducer(reducer, initialState);

  function changeActiveCat(cat) {
    dispatch({ type: "SET_ACTIVE_CAT", payload: { activeCat: cat } });
  }

  function toggleModal(course) {
    dispatch({ type: "SET_IS_OPEN_MODAL", payload: { isOpenModal: !state.isOpenModal } });

    if (course) dispatch({ type: "SET_ACTIVE_MODAL_ID", payload: { activeModalId: course.id } });
    if (course) dispatch({ type: "SET_ACT_COURSE", payload: { activeCourse: course } });
  }

  function toggleEnquiryModal(course) {
    dispatch({ type: "SET_IS_OPEN_ENQUIRY_MODAL", payload: { isOpenEnquiryModal: !state.isOpenEnquiryModal } });

    if (course) dispatch({ type: "SET_ACT_ENQUIRY_COURSE", payload: { activeEnquiryCourse: course } });
    if (course) dispatch({ type: "SET_ACT_COURSE", payload: { activeCourse: course } });

    if (!course) toggleCongratsModal();
  }

  function toggleCongratsModal() {
    dispatch({ type: "SET_IS_OPEN_CONGRATS_MODAL", payload: { isOpenCongratsModal: true } });

    window.setTimeout(() => {
      dispatch({ type: "SET_IS_OPEN_CONGRATS_MODAL", payload: { isOpenCongratsModal: false } });
    }, 5000);
  }

  function populateCoursesAndTabs() {
    dispatch({ type: "SET_IS_LOADING", payload: { isLoading: true } });
    getCourses()
      .then((resp) => {
        const allCourses = resp.data;

        dispatch({ type: "SET_ALL_COURSES", payload: { allCourses } });

        let obj = {};
        let tabsSet = new Set();

        allCourses.forEach((course) => {
          obj[course.category] = [];
          tabsSet.add(course.category);
        });
        allCourses.forEach((course) => {
          obj[course.category].push(course);
        });
        obj["All"] = allCourses;

        const arrTabs = Array.from(tabsSet);
        arrTabs.unshift("All");
        changeActiveCat(arrTabs[0]);

        dispatch({ type: "SET_COURSES_BY_CAT", payload: { coursesByCat: obj } });
        dispatch({ type: "SET_CAT_TABS", payload: { catTabs: arrTabs } });
      })
      .catch((err) => {
        console.log(err);
      })
      .finally(() => {
        dispatch({ type: "SET_IS_LOADING", payload: { isLoading: false } });
      });
  }

  useEffect(() => {
    populateCoursesAndTabs();
  }, []);

  return { state, changeActiveCat, toggleModal, toggleEnquiryModal };
}
