import React, { useEffect, useState } from "react";
import style from "./CourseModal.module.scss";

import clsx from "clsx";

import { getCourse } from "@apis/courses";
import Button from "@components/Button";
import Modal from "@components/Modal";

export default function CourseModal({ activeCourse, toggleEnquiryModal, courseId, isOpen, onClose }) {
  const [course, setCourse] = useState({});

  function populateCourse() {
    getCourse(courseId)
      .then((resp) => {
        setCourse(resp.data);
      })
      .catch((err) => {
        console.log(err);
      });
  }

  useEffect(() => {
    populateCourse();
  }, [courseId]);

  return (
    <Modal isOpen={isOpen} onClose={onClose} className={style.wrapper}>
      {course && (
        <article key={course.title} className={clsx(style.course)}>
          <div className={style.top}>
            <div className={style.title}>{course.title}</div>
            <div className={style.image}>
              <img src={course.image} alt="" />
            </div>
            <div className={style.info}>
              <div className={style.inner}>
                <div className={style.duration}>Duration: {course.course_duration} months</div>
                <div>Category: {course.category}</div>
                <div className={style.fee}>
                  <span>&#8377;</span> {course.course_fee}
                </div>
              </div>
            </div>
          </div>

          <div className={style.content}>
            <div className={style.desc}>{course.description}</div>
            <Button onClick={() => toggleEnquiryModal(activeCourse)}>Enquire Now</Button>
          </div>
        </article>
      )}
    </Modal>
  );
}
